body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100vw;
  height: 100vh;
}

.time-left {
  font-size: 6.854em;
}

.info {
  font-size: 1.333em;
}

.session,
.break {
  display: inline-block;
}

.session {
  margin-right: 20px;
}

#session-length,
#break-length {
  font-size: 32px;
}

.controls--core > button {
  border: none;
  padding: 1em 3em;
  font-size: 1.333em;
  background: #FC5255;
  color: #FFF;
  margin: 1em;
  cursor: pointer;
}

.controls--work {
  margin: 1.33em 0 0.2em 0;
}

.controls--work > button,
.controls--break > button {
  border: none;
  width: 200px;
  height: 40px;
  margin: 0.5em;
  background: #75CBB0;
  color: #FFF;
  font-size: 1em;
  cursor: pointer;
}

